#app-root {
    height: 10em;
    width: 10em;
    background: lightblue;
    overflow: hidden;
}

#modal-root {
    position: relative;
    z-index: 999;
}

.modal {
    background-color: rgba(0,0,0,0.5);
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cropImage__width {
    max-width: 600px;
}